<template>
  <div class="app">
    <el-container class="g_label" direction="horizontal">
      <el-aside width="200px">
        <div class="home-box">
          <div class="home-logo">
            <img :src="this.configGd.logo_home" />
          </div>
          <div class="home-logo_title">{{this.configGd.apptitle}}</div>
        </div>

        <!-- 菜单导航 -->
        <div class="home-menu">
          <el-menu
            unique-opened
            style="border-right: 0"
            background-color="#062a4b"
            text-color="#909cc0"
            active-text-color="#fff"
            :default-active="activeIndex"
            :active="activeIndex"
            @select="handleMenuSelect"
            router
          >
            <NavMenu :navMenus="menuData"></NavMenu>
          </el-menu>
        </div>
      </el-aside>

      <el-container direction="vertical">
        <!-- 头部区域 -->
        <el-header :class="isNoBreadcrumb ? 'el-header-vi' : 'el-header-co'">
          <div class="header-topbar">
            <i>
              <img class="topbar-face" src="../assets/img/userface.png" />
            </i>
            <span class="topbar-name">{{isName}}</span>
            <span class="topbar-role" v-if="isCompanyName">({{isCompanyName}})</span>
            <span class="topbar-role" v-if="this.configGd.isAdmin && isAuth">
              <el-tag size="small" type="danger" style="padding:0 4px;">{{isAuth}}</el-tag>
            </span>
            <el-button type="danger" @click="logout" class="b_logout" icon="el-icon-circle-close">退出</el-button>
          </div>

          <!-- 面包屑导航 -->
          <div v-if="!isNoBreadcrumb" class="home_breadcrumb">
            <Mbreadcrumb></Mbreadcrumb>
          </div>
        </el-header>
        <el-main :class="isNoBreadcrumb ? 'el-main-vi' : 'el-main-co'">
          <router-view></router-view>
        </el-main>

        <!-- 版权区域 -->
        <el-footer :class="isNoBreadcrumb ? 'el-footer-vi' : 'el-footer-co'" height="40px">
          <div class="div_inline">
            <span class="copyright_font">{{this.configGd.copyright}}</span>
          </div>
          <div class="div_inline div_right">
            <span class="font_version">v {{this.configGd.version}}</span>
          </div>
        </el-footer>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import menu from './menu.json'
import NavMenu from './NavMenu.vue'
import Mbreadcrumb from './Mbreadcrumb.vue'

// var data = menu
export default {
  components: {
    NavMenu: NavMenu,
    Mbreadcrumb: Mbreadcrumb
  },
  data() {
    return {
      activeIndex: '设备管理',
      menuData: [],
      breads: [],
      isName: '',
      isRouteName: '',
      isAuth: '',
      isCompanyName: '',
      isNoBreadcrumb: false
    }
  },
  created() {
    if (this.configGd.enterCheck(this)) {
      this.isName = this.$Storage.localGet('username')
      this.isAuth = this.$Storage.localGet('authority_description')
      this.isCompanyName = this.$Storage.localGet('companyName')
    }
  },
  mounted() {
    // this.menuData = menu
    this.menuData = JSON.parse(JSON.stringify(menu))
    this.menuList(this.menuData)

    if (this.configGd.isLoginInt && !this.configGd.isAdmin) {
      this.$router.push(
        '/vidpvideopreview',
        () => {},
        e => {
          console.log('输出报错', e)
        }
      )
    }
    this.configGd.isLoginInt = false
    this.$Storage.localSet('isLoginInt', false)

    this.isRouteName = this.$route.path
    this.changeTabActive()
  },
  destroyed() {
    // this.isAdmin = JSON.parse(sessionStorage.getItem('isAdmin'))
    // this.userMessage = JSON.parse(sessionStorage.getItem('userMessage'))
  },
  methods: {
    // 检索菜单项
    menuList(_menuList) {
      if (!this.configGd.isAdmin) {
        // console.log('menuList')
        for (let i = 0; i < _menuList.length; i++) {
          if (_menuList[i].entity.isAdmin) {
            _menuList.splice(i, 1)
            i--
          } else if (_menuList[i].childs) {
            this.menuList(_menuList[i].childs)
          }
        }
      }
    },

    logout() {
      this.$http
        .get('/api' + '/user/signOut')
        .then(res => {
          this.configGd.clearLogin(this)
          this.$router.push(
            '/login',
            () => {},
            e => {
              console.log('输出报错', e)
            }
          )
        })
        .catch(err => {
          console.log(err)
          this.configGd.clearLogin(this)
        })
    },
    isapi() {
      this.$router.push('/api')
    },

    triggermain(url) {
      this.$router.push(url)
      // 点击切换的时候跳转.自动刷新
      // this.$router.go(0)
    },
    enter() {
      this.isMoust = true
    },
    leave() {
      this.isMoust = false
    },
    handleMenuSelect(index, indexPath) {
      this.breads = indexPath
      this.activeIndex = index
    },
    // 监听路由切换：
    changeTabActive() {
      this.isRouteName = this.$route.path
      this.isNoBreadcrumb = this.$route.path === '/vidpvideopreview'
      var myRouteTitle = ''
      if (this.$route.meta) {
        myRouteTitle = this.$route.meta[this.$route.meta.length - 1].name
      }
      if (myRouteTitle && this.activeIndex !== myRouteTitle) {
        this.activeIndex = myRouteTitle
      }
    }
  },
  watch: {
    $route: 'changeTabActive'
  }
}
</script>

<style lang="less" scoped>
.el-aside {
  width: 200px; //0.45rem
  background: #0d3962;
}
.el-main {
  min-width: 0;
  background-color: #eaedf2;
}
.el-main-co {
  background-color: #eaedf2;
  padding: 5px 20px 2px 20px;
  min-height: calc(100vh - 138px); //138px
}
.el-main-vi {
  background-color: #01101e;
  padding: 0;
  min-height: calc(100vh - 105px); //105px
}

.el-header {
  min-width: 0;
  height: 98px;
}
.el-header-co {
  height: 98px !important;
  background-image: url('../assets/img/topbarbg_co.png');
}
.el-header-vi {
  height: 65px !important;
  background-image: url('../assets/img/topbarbg_vi.png');
}

.el-footer {
  min-width: 0;
  background-color: #fff;
  height: 40px;
  font-size: 13px;
  padding: 8px 0px 0px 16px;
}
.el-footer-co {
  background-color: #fff;
}
.el-footer-vi {
  background-color: #031c35;
}

.b_logout {
  width: 76px;
  height: 32px;
  float: right;
  margin-top: -2px;
  padding: 0px 1px 0px 0px;
}
.home-box {
  width: 200px; //0.45rem
  height: 98px; //0.45rem
  background-image: url('../assets/img/logobg.png');
  .home-logo {
    width: 200px;
    height: 48px;
    text-align: center;
    padding-top: 11px;
    img {
      width: 48px;
      height: 48px;
    }
  }
  .home-logo_title {
    width: 200px; //0.45rem
    color: #fff;
    font-size: 18px;
    font-weight: 900px;
    text-align: center;
    padding-top: 4px;
  }
}
.home-menu {
  width: 200px;
}

.header-topbar {
  height: 60px;
  margin-left: 0px;
  margin-top: 17px;
  .topbar-face {
    margin-bottom: 4px;
  }
  .topbar-name {
    color: #3c4e86;
    margin-left: 6px;
    font-size: 17px;
    font-weight: 600;
  }
  .topbar-role {
    color: #3c4e86;
    margin-left: 5px;
    font-size: 14px;
  }
}
// 面包屑导航
.home_breadcrumb {
  height: 38px;
  margin-left: 0px; //2.25rem
  margin-top: -4px;
}

.el-menu {
  border-right: 0 !important;
}
</style>
