<template>
  <div>
    <el-card class="g_label" v-loading="diaLoading" style="width: 840px; padding: 10px 0 15px 18px">
      <el-row>
        <el-transfer
          :loading="diaLoading"
          filterable
          :titles="['可选权限', '[ '+UsersNames[nowUserIndex]+' ] 已有权限']"
          :filter-method="filterMethod"
          filter-placeholder="请输入权限名"
          v-model="authTransferValue"
          :data="generateData"
          @change="handleChange"
        >
          <div slot-scope="{ option }" style="width: 228px;">
            <div
              style="color: #333333; font-size: 13px; display: inline; width: 80px;"
            >{{ option.label }}</div>
            <div
              style="float: right; color: #8492a6; font-size: 13px; text-align:right; display: inline; width: 140px;"
            >{{ option.description }}</div>
          </div>
        </el-transfer>
      </el-row>
      <el-row style="padding: 0">
        <div aigin="middle" style="text-align:center; padding: 0; margin: -40px 30px 0 0">
          <el-button
            type="primary"
            icon="el-icon-upload"
            @click="saveAuth"
            size="small"
            :loading="diaLoading"
            style="margin-left: 10px;"
          >保存权限</el-button>
        </div>
      </el-row>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      readOnlyFlag: true,
      saveing: false, // 是否正在保存
      // 列表信息
      listdata: [],

      UserNum: 0, // 要添加权限的用户数量
      UsersId: [], // 要添加权限的用户ID数组
      UsersNames: [], // 要添加权限的用户名数组
      UsersAuths: [], // 要添加权限的用户现拥有的权限
      UsersAuthIDs: [], // 要添加权限的用户现拥有的权限ID
      nowUserIndex: 0, // 当前操作的用户索引
      nowUserID: 0, // 当前操作的用户ID
      nowUserName: 0, // 当前操作的用户名

      authTransferValue: [], // 权限Transfer已选列表

      diaLoading: false,

      delId: [],
      currentPage: 1,
      pageSize: 11,
      pageTotal: 0,
      filterMethod(query, item) {
        return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1
        // return true
      }
    }
  },
  computed: {
    // 权限Transfer数据列表
    generateData() {
      var data = []
      if (this.listdata) {
        data = []
        this.listdata.forEach((auth, index) => {
          data.push({
            label: auth.authority_name,
            key: auth.auth_id,
            description: auth.authority_description
          })
        })
      }
      return data
    }
  },
  created() {
    if (this.$Storage.localGet('selId')) {
      this.UsersId = this.$Storage.localGet('selId')
      this.UsersNames = this.$Storage.localGet('selNames')
      this.UserNum = this.UsersId.length
    } else {
      // this.$router.push({ path: '/menmessage' })
      this.$router.push(
        '/mammanmessage',
        () => {},
        e => {
          console.log('输出报错', e)
        }
      )
    }
    this.getAuthList()
    this.getUserAuthAll()
  },
  mounted() {},
  methods: {
    // 获取权限信息
    getAuthList() {
      var apiUrl =
        '/api' +
        '/auth/find/list' +
        '?page=' +
        this.currentPage +
        '&pageSize=' +
        this.pageSize
      this.$http
        .get(apiUrl)
        .then(res => {
          if (res) {
            if (res.data.code === 0) {
              this.listdata = res.data.data.list
              this.pageTotal = res.data.data.total
            } else {
              this.urResponseErr('获取权限信息失败：' + res.data.message)
            }
          } else {
            this.urResponseErr('获取权限信息失败！')
          }
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('获取人员信息失败')
        })
    },

    // 获取用户权限-all
    getUserAuthAll() {
      this.UsersAuths = []
      this.UsersAuthIDs = []
      var myApis = []
      for (let i = 0; i < this.UserNum; i++) {
        myApis[i] = this.$http.get(
          '/api' + '/user/find/auth?' + 'userId=' + this.UsersId[i]
        )
      }
      this.$http
        .all(myApis)
        .then(res => {
          if (res) {
            var myAuths = []
            var myAuthIDs = []
            for (let i = 0; i < res.length; i++) {
              myAuths[i] = []
              myAuthIDs[i] = []
              // this.UsersAuthIDs[i] = []
              if (res[i].data.code === 0) {
                for (let j = 0; j < res[i].data.data.length; j++) {
                  myAuths[i][j] = res[i].data.data[j]
                  myAuthIDs[i][j] = res[i].data.data[j].auth_id
                }
              } else {
                // this.$set(this.UsersAuths[i], 0, '[' + res[i].data.message + ']')
                myAuths[i][0] = '[' + res[i].data.message + ']'
              }
            }
            this.nowUserIndex = 0
            this.nowUserID = this.UsersId[0]
            this.nowUserName = this.UsersNames[0]
            this.UsersAuths = myAuths.slice()
            this.UsersAuthIDs = myAuthIDs.slice()
            // this.authTransferValue = this.UsersAuthIDs[this.nowUserIndex]
            this.authTransferValue = this.UsersAuthIDs[this.nowUserIndex].slice()
            // this.getAuthList()
            this.udShowStyle(false)
          } else {
            this.urResponseErr('获取用户权限失败！')
          }
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          this.urResponseErr('获取用户权限失败！' + err)
        })
    },

    // 保存添加权限
    saveAuth(_e, _noDel) {
      this.udShowStyle(true)
      if (!_noDel) {
        this.delId = []
        for (let i = 0; i < this.UsersAuthIDs[this.nowUserIndex].length; i++) {
          if (
            this.authTransferValue.indexOf(
              this.UsersAuthIDs[this.nowUserIndex][i]
            ) === -1
          ) {
            this.delId.push(this.UsersAuthIDs[this.nowUserIndex][i])
          }
        }
        console.log('this.delId:', this.delId.length, this.delId)
        if (this.delId.length > 0) {
          this.delAuth(this.nowUserID, this.delId, this.saveAuth)
          return
        }
      }

      var apiUrl = '/api' + '/user/save/auth'
      var saveAuth = { userId: this.nowUserID, auths: this.authTransferValue }
      var adTitle = '添加用户权限'
      let config = {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }
      const formData = new FormData()
      for (var I in saveAuth) {
        formData.append(I, saveAuth[I])
      }
      this.$http
        .post(apiUrl, formData, config)
        .then(res => {
          if (res) {
            // console.log('apiSaveUser res:', res, res.data)
            if (res.data.code === 0) {
              this.$message({
                message: adTitle + '成功！',
                type: 'success'
              })
              this.getUserAuthAll()
            } else {
              this.urResponseErr(adTitle + '失败：' + res.data.message)
            }
          } else {
            this.urResponseErr(adTitle + '失败！')
          }
          // this.udShowStyle(false)
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('err:', err)
          this.urResponseErr(adTitle + '失败:' + err)
          // this.udShowStyle(false)
        })
      // eslint-disable-next-line no-unreachable
    },

    // 保存删除用户权限
    delAuth(_userid, _auths, _callback) {
      this.udShowStyle(true)
      var apiUrl = '/api' + '/user/del/auth'
      var saveAuth = { userId: _userid, auths: _auths }
      var adTitle = '移除用户权限'
      let config = {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }
      const formData = new FormData()
      for (var I in saveAuth) {
        formData.append(I, saveAuth[I])
      }
      this.$http
        .post(apiUrl, formData, config)
        .then(res => {
          if (res) {
            // console.log('apiSaveUser res:', res, res.data)
            if (res.data.code === 0) {
              if (_callback) {
                _callback(null, true)
              } else {
                this.$message({
                  message: adTitle + '成功！',
                  type: 'success'
                })
                this.getUserAuthAll()
              }
            } else {
              this.urResponseErr(adTitle + '失败：' + res.data.message)
            }
          } else {
            this.urResponseErr(adTitle + '失败！')
          }
          // this.udShowStyle(false, true)
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('err:', err)
          this.urResponseErr(adTitle + '失败:' + err)
          // this.udShowStyle(false)
        })
      // eslint-disable-next-line no-unreachable
      this.udShowStyle(false)
    },

    // 用户点击删除用户权限
    delAuthHal(_userIndex, _authIndex) {
      console.log(
        'delAuthHal:',
        this.UsersId[_userIndex],
        this.UsersAuths[_userIndex][_authIndex],
        this.UsersAuthIDs[_userIndex]
      )
      this.delAuth(this.UsersId[_userIndex], this.UsersAuthIDs[_userIndex])
    },

    // 提交开始或结束
    udShowStyle(_start) {
      this.diaLoading = _start
    },
    // 提交返回失败的处理：
    urResponseErr(_errmsg) {
      this.udShowStyle(false)
      this.$message.error(_errmsg)
    },

    // 获取是否可以勾选：
    getSelectable(row, index) {
      if (this.UserNum === 1) {
        if (
          this.UsersAuthIDs &&
          this.UsersAuthIDs[this.nowUserIndex] &&
          this.UsersAuthIDs[this.nowUserIndex].indexOf(row.auth_id) >= 0
        ) {
          this.$refs.multipleTable.toggleRowSelection(row, true)
          return false
        }
      }
      return true
    },

    // 批量操作复选框
    handleChange(value, direction, movedKeys) {
      // console.log('handleChange:', value, direction, movedKeys)
      // console.log('handleChange authTransferValue:', this.authTransferValue)
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`)
      this.currentPage = val
      this.getAuthList(this.currentPage)
    },

    // 表单项样式：
    finalCellStyle({ row, column, rowIndex, columnIndex }) {
      return { padding: '7px 0' }
    },

    // 权限小卡样式
    barClass(_index) {
      if (_index % 2 === 0) {
        return 'bar bar-purple-A'
      } else {
        return 'bar bar-purple-B'
      }
    }
  }
}
</script>
<style lang="less">
.bg-purple-dark {
  background: #eeeeee;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-left {
  background: #ffffff;
}
.bar {
  display: inline;
  padding: 2px 4px;
  margin: 0 1px;
  border-radius: 4px;
  border: 1px solid #ddddcd;
}
.bar-purple-A {
  background: #eeeeff;
}
.bar-purple-B {
  background: #ffeeee;
}
.grid-content {
  border-radius: 5px 0 0 5px;
  min-height: 20px;
  margin: 0 3px 0px 0px;
  padding: 4px 8px 0px 8px;
}

.el-transfer {
  font-size: 13px;
}
.transfer-class,
.el-transfer-panel {
  width: 300px;
  min-height: 380px;
  border-radius: 8px;
  // border: 1px solid #ebeef5;
  // overflow: hidden;
  // background: #fff;
  // display: inline-block;
  // vertical-align: middle;
  // -webkit-box-sizing: border-box;
  // box-sizing: border-box;
  // position: relative;
}
.el-transfer-panel__header {
  font-size: 13px;
}
</style>
